@charset "utf-8"; .row { padding: 0 10px; margin: 0 auto } 

/* header */
#header h1 { display: inline-block; position: absolute; top: 10px; left: 1%; color: rgba(0, 0, 0, .9); z-index: 20; cursor:pointer; transition:all .3s; } 
#header h1.scroll { transform: translateY(-6px) scale(90%); } 
.homeLogo.logo1.active { display: none; } 
#header .btn { position: absolute; top: 50%; right: 10px; transform: translateY(-50%); } 
#header { position:relative; } 
#header .headerFixed { position: fixed; top: 0; right: 0; left: 0; background: #fff; z-index: 9999; } 
#header .open { display: none; position: fixed; right: 50px; font-size: 30px; z-index: 999999999999999999; transform: translateY(-50%); cursor: pointer; } 
#header .row { position: relative } 

@keyframes cdAni { 
 0% { transform: translateX(1000px); opacity: 0; } 
 100% { transform: translateY(0); opacity: 1; } 
 }

/* open */
.cd-nav-trigger {display: none; position: absolute; top: 15px; right: 0%; width: 54px;  height: 54px; overflow: hidden; border-radius: 0%; z-index: 99999999999999999999; z-index: 30; background-color: rgba(255,255,255,0.9); text-indent: 100%; white-space: nowrap; -webkit-transition: -webkit-transform 0.5s; -moz-transition: -moz-transform 0.5s; transition: transform 0.5s; transition: .5s all; } 
.cd-nav-trigger.scroll { transform:translateY(-7px) } 
 .cd-nav-trigger .cd-nav-icon { /* icon created in CSS */
position: absolute; top: 50%; right: auto; bottom: auto; left: 50%; width: 30px; height: 2px; -webkit-transform: translateX(-50%) translateY(-50%); -moz-transform: translateX(-50%) translateY(-50%); -ms-transform: translateX(-50%) translateY(-50%); -o-transform: translateX(-50%) translateY(-50%); transform: translateX(-50%) translateY(-50%); background-color: #333; } 
.cd-nav-trigger .cd-nav-icon::before, .cd-nav-trigger .cd-nav-icon:after { /* upper and lower lines of the menu icon */ content: ''; position: absolute; top: 0; right: 0; width: 100%; height: 100%; background-color: inherit; /* Force Hardware Acceleration in WebKit */
-webkit-transform: translateZ(0); -moz-transform: translateZ(0); -ms-transform: translateZ(0); -o-transform: translateZ(0); transform: translateZ(0); -webkit-backface-visibility: hidden; backface-visibility: hidden; -webkit-transition: -webkit-transform 0.5s, width 0.5s, top 0.3s; -moz-transition: -moz-transform 0.5s, width 0.5s, top 0.3s; transition: transform 0.5s, width 0.5s, top 0.3s; } 
.cd-nav-trigger .cd-nav-icon::before { -webkit-transform-origin: right top; -moz-transform-origin: right top; -ms-transform-origin: right top; -o-transform-origin: right top; transform-origin: right top; -webkit-transform: translateY(-10px); -moz-transform: translateY(-10px); -ms-transform: translateY(-10px); -o-transform: translateY(-10px); transform: translateY(-10px); } 
.open1.on .cd-nav-trigger .cd-nav-icon::before { width: 50%; -webkit-transform: translateY(0px) rotate(45deg); -moz-transform: translateY(0px) rotate(45deg); -ms-transform: translateY(0px) rotate(45deg); -o-transform: translateY(0px) rotate(45deg); transform: translateY(0px) rotate(45deg); } 
.cd-nav-trigger .cd-nav-icon::after { -webkit-transform-origin: right bottom; -moz-transform-origin: right bottom; -ms-transform-origin: right bottom; -o-transform-origin: right bottom; transform-origin: right bottom; -webkit-transform: translateY(10px); -moz-transform: translateY(10px); -ms-transform: translateY(10px); -o-transform: translateY(10px); transform: translateY(10px); } 
.open1.on .cd-nav-trigger .cd-nav-icon::after { width: 50%; -webkit-transform: translateY(0px) rotate(-45deg); -moz-transform: translateY(0px) rotate(-45deg); -ms-transform: translateY(0px) rotate(-45deg); -o-transform: translateY(0px) rotate(-45deg); transform: translateY(0px) rotate(-45deg); } 


/* cd */
#header .cd { display:none; position: relative; top: 0; left: 0; height: 100%; z-index: 11; animation: cdAniOut 1.3s; background-color: #fff; } 
#header .cd.active { display: none; } 
#header .cd_full { background: #fff; animation: cdAni 1s; } 


#header .cd_nav { text-align: center; padding-bottom: 0 0 100px; } 
.cd_tab_list ul { display: block; width: 100%; table-layout: fixed; } 
.all_category_block { display: none; margin-top: 80px } 
.all_category_block.on { display: block; } 
.tab_page_cont { display: block; width: 100%; table-layout: fixed; } 
.tab_page_cont li { height: 60px; color: #777; font-size: 18px; border: 1px solid #ddd; background: #f7f7f7; line-height: 60px; border-left: none; box-sizing: border-box; text-align: center; cursor: pointer; } 
.tab_page_cont li:first-child { border-left: 1px solid #ddd; } 
.tab_page_cont li.on { color: #222; font-family: 'Noto_m'; border: 1px solid #222; background: #fff; border-bottom: 1px solid #fff; text-indent: -1px; } 
.tab_page_cont li span { display: block; width: 100%; height: 60px; } 
.tab_page_cont2 { display: table; width: 100%; table-layout: fixed; } 
.tab_page_cont2 li { display: table-cell; height: 60px; color: #777; font-size: 18px; border: 1px solid #ddd; background: #f7f7f7; line-height: 60px; border-left: none; border-bottom: 1px solid #222; box-sizing: border-box; text-align: center; cursor: pointer; } 
.tab_page_cont2 li:first-child { border-left: 1px solid #ddd; } 
.tab_page_cont2 li.on { color: #222; font-family: 'Noto_m'; border: 1px solid #222; background: #fff; border-bottom: 1px solid #fff; text-indent: -1px; } 
.tab_page_cont2 li span { display: block; width: 100%; height: 60px; } 
.tab_page { display:none } 
.tab_page.on { display:block } 
.tab_page2 { display:none } 
.tab_page2.on { display:block } 
.cont_tabs { margin-top: 35px; } 
.box_menu { display: inline-block; width: 180px; margin: 0 8px 48px; overflow: hidden; vertical-align: top; } 
.list_menu { display: inline-block; width: 178px; vertical-align: top; } 
.tit_list_block { padding: 0 0 10px; color: #222; font-size: 18px; overflow: hidden; min-height: 24px; margin-bottom: 10px; border-bottom: 1px solid #222; } 
.emp_blue { display: block; margin: 5px 0; color:#4567ad !important; padding-left: 10px } 
.list_menu span { display: block; height: 30px; color:#555; font-size: 14px; padding-left: 10px; line-height: 30px; } 

#section.active { display: none !important } 

/* nav 구역 */
#nav { position: absolute; top: 0px; left: 0; width: 100%; padding: 0 94px 0 200px; z-index: 13; background-color: #fff; border-bottom: 1px solid #000; } 
#nav.scroll { height:70px; } 
.QnA { position: absolute; top: 22px; right:230px; height:50px; font-size:16px; z-index: 99999999999; }
.QnA i {padding:0 15px 0 0; font-size:25px;}
.Loginscroll { position:absolute;  right:30px; width:140px; height:50px; z-index: 99999999999;} 
.Loginscroll.on { position:absolute;  right:30px; width:200px; height:50px;} 
#nav .userLogin { position: absolute; top: 22px; right:30px; } 
#nav .userLogin span { display: inline-block; transform: translateY(3px);}
#nav .Login { display: block; width:200px; height:50px; color:#000; font-size:16px; text-align: center; cursor: pointer; } 
#nav .Login img { padding:0 20px 0 0; } 
#nav .Logout { border: none; cursor: pointer; }
#nav .Logout img { width: 35px; height: 35px; border-radius: 50%;  } 
#nav .Logout span {display: inline-block; margin:0px 0 0 15px; color:#000; font-size:15px; }
#nav .navUl { margin: 0 auto; overflow: hidden; max-width: 1400px; list-style-type: none; box-sizing: border-box; transition: all .1s; } 
#nav .navUl.scroll { transform: translateY(-4px); } 
#nav .navUl.active { display: none; } 
#nav .navUl .menu { float: left; width: 12%; height:80px; text-align: center; } 
.depth1 { display: block; padding: 0; padding: 0 10px; margin: 0 auto; font-size: 18px; letter-spacing: -0.3px; text-align: center; line-height: 80px; box-sizing: border-box; transition: all .1s; } 
.depth1.scroll { font-size:16px; } 
.depth1>span { transition: all 0.2s; } 
.depth1>span:hover { color: red } 
.depth2>ul { padding: 0; margin: 0; color: #888; list-style-type: none; } 
.depth2>ul>li>span { display: block; height: 70px; padding: 5px 0; border-radius: 5px 5px; transition: all 0.2s; line-height: 30px; } 
.depth2>ul>li>span:hover { color: red } 
#nav .navUl .depth2 { display: block; height:0px; font-size: 14px; overflow: hidden; background:#fff; transition: all 0.1s; } 
#nav .navUl .depth2.active { display: block; height:420px; transition: all 0.3s; } 

/* 질문하기 */
.question { position:fixed; top:20%; right:20px;; width:80px; height:80px; color:#fff; font-weight: bold; border-radius: 50%; background: #4567ad; animation:question 1s infinite linear alternate; text-align: center; cursor: pointer; line-height: 80px;; } 
@keyframes question { 
 0% { margin:0 } 
 100% { margin-top:10px } 
 }

.modal_title { display: flex; align-items: center; margin-bottom: 5px; border-bottom: 1px solid lightgray; } 
.modal_title > h5 {color: yellowgreen; font-size: 20px; font-weight: 500; flex:1; cursor: pointer; margin-right: 30px; } 
.modal_title > h5:hover { color: lightgoldenrodyellow; } 
.modal_info { display: flex; align-items: center; margin-top: 30px; } 
.modal_info > p { color: gray; font-size: small; margin-left: 10px; } 
.modal_info > .modal_scope { display: flex; padding: 5px; color: yellowgreen; border-radius: 33px; align-items: center; margin-left: 10px; background-color:white; cursor: pointer;}
.modal_Field { display: flex; flex-direction: column; } 
.modal_Field textarea { height:150px; } 
.modal_Field > .modal_fieldLink { display: flex; color : yellowgreen; margin-top: 10px; align-items: center; }
.modal_Field > .modal_fieldLink > input { border: none; flex: 1; outline: none; margin-left : 5px; } 
.modal_buttons { display: flex; align-items: center; margin-right: -20px; border-top: 1px solid lightgray; } 
.modal_buttons > .add { padding: 10px; color:yellowgreen; font-weight: 700; border: none; border-radius: 33px; outline: none; margin-top: 5px; background-color:yellowgreen; margin-right: 10px; cursor: pointer; } 



