.jejuArticle { position:relative; } 
.jejuArticle .slide { display: flex; position:relative; width:100%; height:500px; } 
.jejuArticle .slide.slide1 { background: url(./img/main_img1.jpg) no-repeat center; background-size:contain } 
.jejuArticle .slide.slide2 { background: url(./img/main_img2.jpg) no-repeat center; background-size: contain; } 
.jejuArticle .slide.slide3 { background: url(./img/main_img3.jpg) no-repeat center; background-size: contain; } 
.jejuArticle .slide.slide4 { background: url(./img/main_img4.jpg) no-repeat center; background-size: contain; } 

.jeju_insert { width:100%; padding:100px 0 20px; text-align: center; } 
.jeju_insert_center { width:1300px; margin: 0 auto;}
.jeju_insert h2 { padding:0 0 30px; font-size:30px; } 
.jeju_insert ul.insert_link { display: flex; padding: 0 0 50px; justify-content: center; } 
.jeju_insert ul.insert_link span { border:1px solid #bbb; flex:1; line-height: 50px; cursor: pointer; } 
.jeju_insert ul.insert_link span:hover { color:#fff; background-color:skyblue; } 


.jeju_MAP { position: relative; width: 100%; height: 470px; margin-bottom: 40px; } 
.jeju_MAP .Map_left { float:left; width:70%; } 
.jeju_MAP .jejuSection { float:right; width:30%; } 

.jeju_img { width:894px; height:500px; background: url(./img/ico_map_jeju.jpg)no-repeat center; background-size: cover; } 
.jeju_MAP i { position:absolute; width:100px; height:100px; font-size: 20px; border-radius: 100%; background:#fff; line-height: 100px; cursor: pointer; } 
.jeju_MAP i.active { color:#fff; background-color:skyblue; } 
.jeju_MAP i.jeju_jungmoon { top: 343px; left: 199px; } 
.jeju_MAP i.jeju_seoguipo { top: 341px; left: 423px; } 
.jeju_MAP i.jeju_aewoul { top: 81px; left: 171px; } 
.jeju_MAP i.jeju_jejusi { top: 24px; left: 372px; } 
.jeju_MAP i.jeju_sungsan { top: 179px; left: 664px } 
.jeju_MAP i:hover { color:#fff; background-color:skyblue; } 

.jungmoon,
.seoguipo,
.aewoul,
.jejusi,
.sungsan { display: none; } 


.jejuSection > div ul li { width:100%; height:166px; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; }
.jungmoon.active { display: block; } 
