.clickActive { padding:50px 0 0; } 
.clickActive h2 { text-align: center; font-size: 30px; padding:0 0 50px } 
.clickActive ul { display:flex; justify-content: center; margin:0 auto; text-align: center; width:70%; } 
.clickActive ul li { flex:1; border:1px solid #bbb; line-height: 50px; cursor: pointer; } 
.clickActiveLi { background-color: none; } 
.clickActiveLi.active { background-color: #ededed; border-bottom:none } 
.section1 { display: flex; width:100%; height:420px; background-color: #ededed; } 
