@charset "utf-8"; @media screen and (max-width:1540px){
 .QnA { position: absolute;  right:240px; font-size:16px; } 
#nav .userLogin { position: absolute; right:101px; } 
#nav .Logout { width:120px;}
#nav .userLogin span { display: inline-block; transform: translateY(3px);}
 .depth1 { display: none; } 
 .cd-nav-trigger { display:block; right:3% } 
 #header .cd.active { display: block; position: fixed; width: 100%; height:auto } 
 .cdLi.active { display: none; } 
 #nav .navUl.scroll { transform: translateY(-4px); display: none; } 
 }
 @media screen and (max-width:600px){
 .QnA { position: absolute; right:180px; font-size:14px; } 

#nav .userLogin { position: absolute; right:80px; width:80px;  } 
#nav .Login { display: block; width:100px; height:50px; color:#000; font-size:16px;  text-align: center; cursor: pointer; } 
#nav .Login img { padding:0 10px 0 0;}
#nav .Logout { top:22px !important;}
#nav .Logout img { width: 30px; height: 30px; border-radius: 50%;  } 
#nav .userLogin span {display: inline-block;  color:#000; font-size:13px; transform: translateY(3px);}

 }
 @media screen and (min-width:1540px){
 #nav .navUl.active { display: block; } 
 }